export default {
	data() {
		return {
			name: '',
			avatarUrl: ''
		}
	},
	onLoad() {

	},
	methods: {
		login() {
			var that = this;
			uni.login({
				provider: 'weixin',
				success: function(loginRes) {
					// console.log(loginRes);
					// 获取用户信息
					uni.getUserInfo({
						provider: 'weixin',
						success: function(infoRes) {
							// console.log(infoRes)
							that.name = infoRes.userInfo.nickName
							that.avatarUrl = infoRes.userInfo.avatarUrl
						}
					});
				}
			});
		},
		shipping(){
			uni.navigateTo({
				url:'../site/site'
			})
		},
		shop(){
			uni.switchTab({
				url:'../shopping/shopping'
			})
		}
	}
}
